  methods: {
    watchDialog() {
      console.log("dialog watched");
    },
    checkScroll() {
      if (window.scrollY > 0) {
        this.isScrolled = true;
      } else {
        this.isScrolled = false;
      }
    },
    scrollToTop() {
      window.scrollTo({
        top: 0,
        behavior: "smooth",
      });
    },
    scrollToSection(section) {
      const SECTION = document.getElementById(section);
      if (!SECTION) return;
      const offset = section == "Home" ? 0 : SECTION.offsetTop;
      scrollTo({
        behavior: "smooth",
        top: offset,
      });
    },
    submitForm() {
      if (this.$refs.form.validate()) {
        this.isSubmitBtnClicked = true;
        setTimeout(() => {
          swal
            .fire({
              title: "Message Sent",
              icon: "success",
              showConfirmButton: true,
              timer: 2800,
              allowOutsideClick: false,
              allowEscapeKey: false,
              timerProgressBar: true,
            })
            .then(() => {
              this.isSubmitBtnClicked = false;
              this.$refs.form.reset();
              this.name = "";
              this.email = "";
              this.project = "";
            });
        }, 750);
      }
    },
    checkBarScroll() {
      if (window.scrollY == 0) {
        this.barScroll = true;
      } else {
        this.barScroll = false;
      }
      window.onscroll = () => {
        if (window.scrollY == 0) {
          this.barScroll = true;
        } else {
          this.barScroll = false;
        }
      };
    },
    switchMode() {
      this.$vuetify.theme.dark = !this.$vuetify.theme.dark;
      this.darkMode = this.$vuetify.theme.dark;
      localStorage.setItem("portfolioDarkMode", this.$vuetify.theme.dark);
    },
    checktheCurrentMode() {
      this.darkMode = this.$vuetify.theme.dark;
    },
    setActiveSectionWhenScroll() {
      this.homeLinks.forEach((el) => {
        let SECTION = document.getElementById(`${el.section}`);
        if (!SECTION) return;
        if (window.scrollY >= SECTION.offsetTop) {
          this.activeSection = el.id;
        }
      });
      window.onscroll = () => {
        this.homeLinks.forEach((el) => {
          let SECTION = document.getElementById(`${el.section}`);
          if (!SECTION) return;
          if (window.scrollY >= SECTION.offsetTop) {
            this.activeSection = el.id;
          }
        });
      };
    },
    checkPageLoaded() {
      document.onreadystatechange = () => {
        if (document.readyState == "complete") {
          setTimeout(() => {
            this.pageLoaded = false;
            this.dialog = false;
          }, 1500);
        }
      };
    },
    getTheDarkMode() {
      if (localStorage.getItem("portfolioDarkMode")) {
        this.darkMode = JSON.parse(localStorage.getItem("portfolioDarkMode"));
        this.$vuetify.theme.dark = JSON.parse(
          localStorage.getItem("portfolioDarkMode")
        );
        // console.log(JSON.parse(localStorage.getItem("portfolioDarkMode")));
      }
    },
    downloadCv(media) {
      // eslint-disable-next-line no-unused-vars
      var openCvInWindow = () => {};
